@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-family: 'Montserrat', sans-serif;
  background: linear-gradient(
    to right bottom,
    #020105,
    #060514,
    #0a0920,
    #0a0a23,
    #060516
  );
  height: 100vh;
}

@media (min-width: 1600px) {
  html {
    font-size: 80%;
  }
}

@media (max-width: 1200px) {
  html {
    font-size: 57.5%;
  }
}

@media (max-width: 900px) {
  html {
    font-size: 55%;
  }
}

@media (max-width: 600px) {
  html {
    font-size: 50%;
  }
}

@media (max-width: 400px) {
  html {
    font-size: 45%;
  }
}

@media (max-width: 350px) {
  html {
    font-size: 40%;
  }
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header--image__container {
  margin-left: 2rem;
  padding: 2.5rem;
  display: flex;
  align-items: center;
}

.header--image__container-img {
  height: 5rem;
}

.header--image__container-label {
  margin-left: 2rem;
  font-size: 1.75rem;
  font-weight: bold;
  color: white;
}

.header--nav {
  align-self: stretch;
}

.navigation__ul {
  height: 100%;
  list-style: none;
  display: flex;
  align-items: center;
  margin-right: 2rem;
}

.navigation__ul--li a {
  padding: 1rem;
  margin-right: 2rem;
  font-size: 1.5rem;
  color: white;
  text-decoration: none;
  font-weight: bold;
  outline: none;
}

.navigation__ul--li a:hover {
  color: #ccc;
}

.home__container {
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 45%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.home__container--h1 {
  padding: 1rem;
  text-align: center;
  font-size: 3.5rem;
  font-weight: bold;
  color: white;
  text-transform: capitalize;
}

.home__container--p {
  padding: 2rem;
  text-align: center;
  font-size: 1.5rem;
  font-weight: bold;
  color: #ccc;
  text-transform: capitalize;
}

.home__btn {
  width: 15rem;
  margin: 2.25rem;
}

.home__btn a {
  display: block;
  width: 100%;
  height: 100%;
  padding: 1.5rem;
  border-radius: 3rem;
  background-color: #3672f8;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 0.4s;
  text-decoration: none;
  cursor: pointer;
  position: relative;
}

.home__btn a::before {
  content: '';
  display: inline-block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0%;
  left: 0%;
  border-radius: 3rem;
  background-color: #2268ffec;
  z-index: -1;
  transition: all 0.4s;
}

.home__btn a:hover {
  background-color: #2268ffec;
  transform: translateY(-0.5rem);
}

.home__btn a:hover::before {
  transform: scale(1.5);
  opacity: 0;
}

.home__btn a:active {
  transform: translateY(-0.25rem);
}
.home__btn--label {
  font-weight: bold;
  color: white;
  font-size: 1.5rem;
  margin-left: 1.25rem;
  cursor: pointer;
  text-transform: uppercase;
}

.home__btn--circle {
  width: 2.5rem;
  border-radius: 1.25rem;
  height: 2.5rem;
  background: white;
  margin-right: 0.25rem;
  position: relative;
}

.home__btn--circle::before {
  content: '';
  position: absolute;
  width: 15%;
  height: 40%;
  background-color: #3672f8;
  top: 24.5%;
  left: 50%;
  transform: rotate(-45deg);
}

.home__btn--circle::after {
  content: '';
  position: absolute;
  width: 15%;
  height: 40%;
  background-color: #3672f8;
  bottom: 17.5%;
  left: 50%;
  transform: rotate(45deg);
}

.home__animated--box {
  padding: 1rem 1.5rem;
  border-radius: 1rem;
  width: 10rem;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #141414;
  box-shadow: 0 1rem 2rem rgba(255, 255, 255, 0.1),
    0 -1rem 2rem rgba(255, 255, 255, 0.1);
  cursor: pointer;
  transition: all 0.4s;
}

.home__animated--box:hover {
  transform: scale(1.05);
}

.home__animated--box__img {
  height: 4rem;
}

.home__animated--box__label {
  color: rgb(170, 170, 170);
  font-size: 1.5rem;
  font-weight: bold;
  margin-left: 1rem;
  cursor: pointer;
}

.home__animated--box--1 {
  position: absolute;
  top: 20%;
  left: 5%;
  -webkit-animation: LeftToRightFadeIn 1s;
          animation: LeftToRightFadeIn 1s;
}

.home__animated--box--2 {
  position: absolute;
  top: 25%;
  right: 5%;
  -webkit-animation: RightToLeftFadeIn 1s;
          animation: RightToLeftFadeIn 1s;
}

.home__animated--box--3 {
  position: absolute;
  bottom: 15%;
  left: 5%;
  -webkit-animation: LeftToRightFadeIn 1s;
          animation: LeftToRightFadeIn 1s;
}

.home__animated--box--4 {
  position: absolute;
  bottom: 10%;
  right: 5%;
  -webkit-animation: RightToLeftFadeIn 1s;
          animation: RightToLeftFadeIn 1s;
}

@media (max-width: 1110px) {
  .home__container {
    width: 55%;
  }
}

@media (max-width: 870px) {
  .home__container {
    width: 65%;
  }

  .home__container--h1 {
    padding: 0rem;
    font-size: 3rem;
  }

  .home__container--p {
    padding: 2rem;
    font-size: 1.5rem;
  }
}

@media (max-width: 735px) {
  .home__container {
    width: 75%;
  }
}

@media (max-width: 480px) {
  .home__container {
    width: 90%;
  }
}

@-webkit-keyframes LeftToRightFadeIn {
  from {
    transform: translateX(-5rem);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes LeftToRightFadeIn {
  from {
    transform: translateX(-5rem);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@-webkit-keyframes RightToLeftFadeIn {
  from {
    transform: translateX(5rem);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes RightToLeftFadeIn {
  from {
    transform: translateX(5rem);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.converter__form {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.converter__currency__container {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 1rem;
}

.converter__form--input--h1 {
  color: #fff;
  font-weight: bold;
  font-size: 1.75rem;
  margin: 1.5rem;
}

.converter__form--input {
  padding: 0.6rem;
  width: 10rem;
  margin: 0 2rem;
  background-color: transparent;
  border: none;
  border-bottom: 0.1px solid #ccc;
  text-align: center;
  outline: none;
  color: #fff;
  font-weight: bold;
  text-transform: uppercase;
  -moz-appearance: textfield;
}

.converter__form--input::-webkit-outer-spin-button,
.converter__form--input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.converter__form--btn {
  margin-top: 2rem;
  padding: 1.25rem;
  font-weight: bold;
  color: white;
  text-transform: uppercase;
  border: none;
  outline: none;
  border-radius: 2rem;
  width: 20rem;
  background-color: #3672f8;
  cursor: pointer;
  transition: all 0.4s;
  position: relative;
}

.converter__form--btn::before {
  content: '';
  display: inline-block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0%;
  left: 0%;
  border-radius: 3rem;
  background-color: #2268ffec;
  z-index: -1;
  transition: all 0.4s;
}

.converter__form--btn:hover {
  background-color: #2268ffec;
  transform: translateY(-0.5rem);
}

.converter__form--btn:hover::before {
  transform: scale(1.5);
  opacity: 0;
}

::-webkit-input-placeholder {
  color: #777;
}

:-ms-input-placeholder {
  color: #777;
}

::-ms-input-placeholder {
  color: #777;
}

::placeholder {
  color: #777;
}

.converter__container--select {
  margin-bottom: 2rem;
  padding: 1rem;
  width: 15rem;
  border-radius: 0.5rem;
  font-weight: bold;
  background-color: #ccc;
  color: #141414;
  cursor: pointer;
  outline: none;
  border: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  /* display: none; */
}

.modal__backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.97);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal__container {
  border: 3px solid #2268ffec;
  background-color: #141414;
  border-radius: 0.5rem;
  box-shadow: 0 0rem 5rem rgba(255, 255, 255, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 30%;
  padding: 3rem;
  -webkit-animation: zoomIn 0.2s ease-in-out;
          animation: zoomIn 0.2s ease-in-out;
  transition: all 0.3s;
  transform-origin: center;
}

.modal__text--container {
  width: 50%;
  display: flex;
  padding: 1rem;
  justify-content: space-between;
  align-items: center;
}

.modal__text--container__text {
  color: #ccc;
  font-size: 1.75rem;
}

.modal__text {
  padding: 0.95rem;
  margin: 1.5rem;
  color: white;
  border-radius: 1rem;
  text-transform: uppercase;
}

.modal__text--amount {
  border: 2px solid tomato;
}

.modal__text--result {
  border: 2px solid #e45;
}

.modal__btn {
  margin-top: 2rem;
  padding: 1.25rem;
  font-weight: bold;
  color: white;
  text-transform: uppercase;
  border: none;
  outline: none;
  border-radius: 2rem;
  width: 20rem;
  background-color: #3672f8;
  cursor: pointer;
  transition: all 0.4s;
  position: relative;
}

.modal__btn::before {
  content: '';
  display: inline-block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0%;
  left: 0%;
  border-radius: 3rem;
  background-color: #2268ffec;
  z-index: -1;
  transition: all 0.4s;
}

.modal__btn:hover {
  background-color: #2268ffec;
  transform: translateY(-0.5rem);
}

.modal__btn:hover::before {
  transform: scale(1.5);
  opacity: 0;
}

@-webkit-keyframes zoomIn {
  from {
    opacity: 0;
    transform: translateY(-12.5rem);
  }
  to {
    opacity: 1;
    transition: translateY(0);
  }
}

@keyframes zoomIn {
  from {
    opacity: 0;
    transform: translateY(-12.5rem);
  }
  to {
    opacity: 1;
    transition: translateY(0);
  }
}

